import React, {useEffect, useState} from "react";
import axios from "axios";
import {Link, useNavigate} from "react-router-dom";

function Boards() {
    const navigate = useNavigate();
    const [boards, setBoards] = useState([]);

    // 
    const [paging, setPaging] = useState({
        page: '1',
        pageSize: '4',
        rowSize: '5',
        startRow: '',
        endRow: '',
        startPage: '',
        endPage: '',
        totalBoardCount: '',
        totalPageCount: '',
    });

    const [search, setSearch] = useState({
        page : '1',
        searchCondition :'',
        searchValue :''
    });

    async function getBoards() {
        //  -> 
        let queryString = new URLSearchParams(search).toString();


        //  default    default   
        queryString += "&pageSize=3&rowSize=5";
        console.log(" ",queryString);
        const response = await axios.get(`//localhost:8989/board/boards?${queryString}`);

        //  
        setBoards(response.data.data);
        console.log("",response.data);

        //  
        setPaging({
            ...response.data,
        });
    }

    useEffect(() => {
        //  1
       getBoards();
    }, [search]);

    function moveToWrite() {
        navigate('/board/write');
    }

    function handlePageClick(e){
        console.log("",e.target.value);
        setSearch({...search, page : e.target.value});
        //getBoards();
    }

    function pageRender(){
        const pageCnt = paging.endPage - paging.startPage;
        const result = [];

        if(paging.startPage > 1){
            result.push(<button key={"previous"} onClick={handlePageClick} value={paging.startPage-1}></button>);
        }

        for(let i=paging.startPage; i<=paging.endPage; i++){
            result.push(<button key={i} onClick={handlePageClick} value={i}>{i}</button>);
        }

        if(paging.endPage < paging.totalPageCount){
            result.push(<button key={"next"} onClick={handlePageClick} value={paging.endPage+1}></button>);
        }
        return result;
    }

    return (
        <div>
            <h3> </h3>
            <ul>
                {boards.map((board,index) => (
                        <li key={index}>
                            <Link to={`/board/${board.id}`}> {board.title} </Link>
                        </li>
                    )
                )}
            </ul>
            <div>
                {pageRender()}
            </div>
            <div>
                <button onClick={moveToWrite}></button>
            </div>
        </div>
    );
}

export default Boards;